/*----------------------
  Footer Section
------------------------*/

.footer-section {
  padding: 18px 80px 16px;
  /* height: 100%; */
	overflow: hidden;
	background: #252525;
  clear: both;
  position: relative;
  height: 50px;
}

.footer-section .container {
	display: initial;
}

.footer-menu {
	list-style: none;
	float: right;
}

.footer-menu li {
	display: inline;
}

.footer-menu li a {
	display: inline-block;
	font-size: 14px;
	font-weight: 500;
	color: #aeaeae;
	margin-left: 30px;
}

.copyright {
	float: left;
	margin-bottom: 0;
	font-weight: 500;
	color: #aeaeae;
}