.Endless {
  display: inline-flex;
  width: 100%;
  height: 85vh;
}

.EndlessLeft{
  flex: 70%;
  background: #ff0000;
  display: inline-grid;
}

.EndlessBetList{
  flex: 30%;
  background: #ffbb00;
}

.EndlessChart{
  flex: 80%;
  background: #00ff00;
  height: 70vh;
  display: grid;
}

.EndlessController{
  flex: 20%;
  background: #00ffff;
  height: 15vh;
}